/* Header */
.bg-light {
  background-color: #f8f9fa !important;
}
.header-controls-pics {
  position: relative;
  display: flex;
  justify-content: space-between;
  max-width: 280px;
  margin-left: auto;
}
.header-controls-pic {
  height: 32px;
  width: 32px;
  margin: 0 10px;
  background-image: url(../../img/header-controls-sprite.png);
  background-repeat: no-repeat;
  cursor: pointer;
}
.header-controls-search {
  position: relative;
  z-index: 1;
  background-position: -64px 0;
}
.header-controls-profile {
  position: relative;
  background-position: -32px 0;
}
.header-controls-cart {
  position: relative;
  background-position: 0 0;
}
.header-controls-cart-full {
  position: absolute;
  right: -10px;
  top: -5px;
  display: block;
  height: 21px;
  width: 21px;
  font-size: 11px;
  line-height: 21px;
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background: linear-gradient(90deg, #ff65a5 1.11%, #ff8076 100%);
  box-shadow: 0px 2px 8px rgba(255, 101, 165, 0.6), inset 0px 1px 1px #ff8076;
}

.navbar {
  display: flex;
  gap: 20px;
  padding: 1rem 1rem;
}

.collapse {
  display: flex;
  align-items: center;
  width: 100%;
}
.navbar-nav {
  display: flex;
  gap: 20px;
}
.mr-auto {
  margin-right: auto;
}

.active-header {
  color: #000;
}
.nav-link:hover {
  transition: 0.3s;
  color: #000;
}
